@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@font-face{font-family:"Monument";src:url(/showtime-composer/static/media/Monument-Regular.f35eddda.ttf) format("opentype");font-weight:400}@font-face{font-family:"Monument";src:url(/showtime-composer/static/media/Monument-Bold.a6b8eaec.ttf) format("opentype");font-weight:700}*{margin:0;padding:0;box-sizing:border-box}.grain{position:absolute;width:100%;height:100%;overflow:hidden;background:radial-gradient(ellipse at center, #ffeeac 0%, #5b2c15 100%)}.grain::after{content:"";position:absolute;width:200%;height:200%;left:-50%;top:-50%;background-image:url(data:image/png;base64,);animation:grain 1.5s steps(6) infinite}@keyframes grain{0%,100%{transform:translate(0, 0)}10%{transform:translate(-5%, -10%)}30%{transform:translate(3%, -15%)}50%{transform:translate(12%, 9%)}70%{transform:translate(9%, 4%)}90%{transform:translate(-1%, 7%)}}*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins"}.app{height:100vh;display:flex}.app .action-area{position:relative;max-width:450px;width:100%;padding:20px 30px;overflow-y:auto}.app .player-area{flex:1 1;padding:20px;padding:20px}.app .player-container{width:100%;height:100%;padding:10px;background-color:#fff;text-align:center;border-radius:8px;box-shadow:5px 5px 25px rgba(0,0,0,0.4);position:relative}.app .player-overlay{position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(255,255,255,0.4);display:flex;align-items:center;justify-content:center;text-align:center;font-size:32px}.wrapper{width:1920px;height:1080px;background-color:#161312;position:relative;display:flex;justify-content:center;align-items:center}.title{font-family:"Monument";color:#cd2223;font-size:96px;text-transform:uppercase;position:absolute}.title--relative{position:relative}.title--inline{display:inline-block}.title--outlined{color:transparent;-webkit-text-stroke:2px #cd2223}.title--transparent{color:transparent}.title--rotate{font-size:32px;height:150px;position:absolute;width:20px;left:0;top:0;transform-origin:bottom center}.square{height:450px;padding:15px;position:absolute;background-color:#fff;border:2px solid #fff}.square--outlined{background-color:transparent}.control-wrapper{height:100vh;padding:20px;overflow-x:auto;min-width:300px;max-width:450px;width:100%;position:relative;z-index:1}.button{padding:10px;background-color:#fff;text-align:center;border-radius:8px;margin-bottom:10px;cursor:pointer;box-shadow:5px 5px 25px rgba(0,0,0,0.4)}.button--disabled{opacity:0.6 !important;pointer-events:none}.person-wrapper{border-radius:10px;margin-bottom:10px;width:100%;padding:25px 28px;position:relative;background:white;border-radius:8px;display:flex;align-items:center;flex-shrink:0;box-shadow:5px 5px 25px rgba(0,0,0,0.4)}.person-wrapper .avatar{width:100px;height:100px;border-radius:50%;background-color:lightgrey;margin-right:18px;display:flex;justify-content:center;align-items:center;color:#fff}.person-wrapper .avatar input{position:absolute;top:0;left:0;width:1px;height:1px}.person-wrapper .info{display:flex;flex-direction:column;flex:1 1}.person-wrapper input{width:100%;padding:5px 10px;margin-bottom:5px}.close{position:absolute;top:5px;right:5px;font-size:14px;cursor:pointer}.modal-backdrop{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.4);display:flex;justify-content:center;align-items:center;z-index:2}.modal-container{max-width:80%;max-height:80%;width:100%;height:100%;position:relative;background-color:#fff;padding:20px;border-radius:8px}

